import PropTypes from 'prop-types';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import { ChromePicker } from 'react-color';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

export default function ColorControl({
  color,
  displayColorPicker,
  handleClick,
  handleClose,
  handleChange,
}) {
  console.log('color control', color, displayColorPicker);
  return (
    <Stack
      direction="row"
      alignItems="center"
      justifyContent="space-between"
      spacing={2}
      sx={{ my: 2 }}
    >
      <Typography
        sx={{ fontWeight: 'bold', color: 'gray', fontSize: '0.87rem' }}
      >
        Background Color
      </Typography>
      <Box
        component="div"
        sx={{
          padding: '2px',
          background: '#fff',
          borderRadius: '5px',
          boxShadow: '0 0 0 1px rgba(0,0,0,.1)',
          display: 'inline-block',
          cursor: 'pointer',
        }}
        onClick={handleClick}
      >
        <Box
          component="div"
          sx={{
            width: '70px',
            height: '30px',
            borderRadius: '18px',
            background: `rgba(${color.r}, ${color.g}, ${color.b}, ${color.a})`,
          }}
        />
      </Box>
      <ColorPicker
        open={displayColorPicker}
        color={color}
        handleClose={handleClose}
        handleChange={handleChange}
      />
    </Stack>
  );
}

ColorControl.propTypes = {
  color: PropTypes.shape({
    r: PropTypes.number.isRequired,
    g: PropTypes.number.isRequired,
    b: PropTypes.number.isRequired,
    a: PropTypes.number.isRequired,
  }).isRequired,
  displayColorPicker: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired,
  handleChange: PropTypes.func.isRequired,
  handleClick: PropTypes.func.isRequired,
};

export function ColorPicker({ open, color, handleClose, handleChange }) {
  console.log(handleClose);
  return open ? (
    <>
      <Box
        component="div"
        sx={{
          position: 'absolute',
          zIndex: 3,
          bottom: 10,
        }}
      >
        <IconButton
          sx={{
            position: 'absolute',
            top: 0,
            right: 0,
            zIndex: 2,
            backgroundColor: 'button.main',
            p: 0,
            transform: 'translate(0, -110%)',
            border: '2px solid black',
          }}
          onClick={handleClose}
        >
          <CloseIcon />
        </IconButton>
        <ChromePicker color={color} onChange={handleChange} />
      </Box>
    </>
  ) : null;
}

ColorPicker.propTypes = {
  color: PropTypes.shape({
    r: PropTypes.number.isRequired,
    g: PropTypes.number.isRequired,
    b: PropTypes.number.isRequired,
    a: PropTypes.number.isRequired,
  }).isRequired,
  open: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired,
  handleChange: PropTypes.func.isRequired,
};
