import { useState, useEffect, useRef } from 'react';
import db from 'db';

import useCollageTree from 'hooks/collageTree';
import useIndexedDBState from 'hooks/indexedDBState';
import useImageCount from 'hooks/imageCount';

import helpers from 'components/collage/helpers';
import treeUtils from 'components/collage/treeUtils';
import Header from 'components/collage/Header';
import DownloadDialog from 'components/collage/DownloadDialog';
import Collage from 'components/collage/Collage';
import Loading from 'components/Loading';
import View from 'components/collage/View';

import MobileActions from 'components/collage/MobileActions';
import MobileControls from 'components/collage/MobileControls';
import DesktopActions from 'components/collage/DesktopActions';
import DesktopControls from 'components/collage/DesktopControls';
import DesktopImageOptions from 'components/collage/DesktopImageOptions';
import EditControl from 'components/collage/EditControl';

function CollageMaker() {
  const tolerance = 0.1;
  const resizeImageWidth = 1500;
  const paperSizes = {
    A4: { width: 2480, height: 3508, text: '8 X 11 inches' },
    A3: { width: 3508, height: 4961, text: '11 X 16 inches' },
  };

  const headerRef = useRef();
  const actionsRef = useRef();
  const footerRef = useRef();
  const aspectRatio = 0.7;
  const addImageRef = useRef();

  const [newImgCount, setNewImageCount] = useState(0);
  const [newImagesLoaded, setNewImagesLoaded] = useState(0);
  const [imgCount, setImageCount] = useImageCount('imgCount', 0);
  const [loading, setLoading] = useState(false);
  const [images, setImages, addToImagesDB] = useIndexedDBState(
    'images',
    [],
    setLoading
  );
  const [originalImages, setOriginalImages, addToOriginalImagesDB] =
    useIndexedDBState('originalImages', []);

  const [printOptions, setPrintOptions] = useState({
    paperWidth: 0,
    paperHeight: 0,
  });
  const [generate, setGenerate] = useState(false);
  const [height, setHeight] = useState(0);
  const [imageHeight, setImageHeight] = useState(0);
  const [imageWidth, setImageWidth] = useState(0);
  const [refresh, setRefresh] = useState(false);
  const [removeImage, setRemoveImage] = useState(false);
  const [width, setWidth] = useState(0);
  const [maxHeight, setMaxHeight] = useState(0);
  const [maxWidth, setMaxWidth] = useState(0);
  const [open, setOpen] = useState(false);
  const [displayColorPicker, setDisplayColorPicker] = useState(false);

  const [options, setOptions] = useState({
    padding: 2,
    borderRadius: 5,
    color: {
      r: 255,
      g: 255,
      b: 255,
      a: 1,
    },
    text: '',
    topText: true,
  });

  const [textHeight, setTextHeight] = useState(0);
  const [tree, setTree, addTreeToDB] = useCollageTree({}, setOptions);

  const handleClear = () => {
    db.collageDB.collageTree.clear();
    setTree({});
    db.collageDB.images.clear();
    setImages([]);
    db.collageDB.originalImages.clear();
    setImages([]);
    db.collageDB.imgCount.clear();
    setImageCount(0);
    setOptions({
      padding: 2,
      borderRadius: 5,
      color: {
        r: 255,
        g: 255,
        b: 255,
        a: 1,
      },
      text: '',
      topText: true,
    });
  };

  const setDimensions = () => {
    const headerHeight = headerRef.current ? headerRef.current.offsetHeight : 0;
    const actionsHeight = actionsRef.current
      ? actionsRef.current.offsetHeight
      : 0;
    const footerHeight = footerRef.current ? footerRef.current.offsetHeight : 0;
    const otherHeight = headerHeight + actionsHeight + footerHeight;
    setMaxWidth(window.innerWidth);
    setMaxHeight(window.innerHeight - otherHeight);
    const [w, h] = helpers.getCollageDimension(
      window.innerWidth,
      window.innerHeight - otherHeight - 6,
      aspectRatio
    );
    console.log('other height', otherHeight);
    setWidth(w);
    setHeight(h);
  };

  useEffect(() => {
    console.log('dimensions changed');
    setDimensions();
  }, [
    window.innerHeight,
    window.innerWidth,
    headerRef.current,
    actionsRef.current,
    footerRef.current,
  ]);

  useEffect(() => {
    if ((newImgCount > 0 && newImgCount === newImagesLoaded) || refresh) {
      setNewImageCount(0);
      setNewImagesLoaded(0);
      const [, bestAspectRatioTree] = treeUtils.findBestAdjustTree(
        images,
        tolerance,
        width,
        height - textHeight
      );
      bestAspectRatioTree.addOptions(options);
      setTree(bestAspectRatioTree);
      db.collageDB.collageTree.clear();
      addTreeToDB(bestAspectRatioTree);
      setRefresh(false);
    }
  }, [newImgCount, newImagesLoaded, refresh]);

  const collageCallback = (someTree) => {
    db.collageDB.collageTree.clear();
    addTreeToDB(someTree);
  };

  const handleRemoveImage = (imgTitle) => {
    const removeIndex = images.findIndex((elem) => elem.title === imgTitle);
    const newImagesArr = [
      ...images.slice(0, removeIndex),
      ...images.slice(removeIndex + 1),
    ];
    setImages(newImagesArr);
    setImageCount((cnt) => cnt - 1);
    const [, bestAspectRatioTree] = treeUtils.findBestAdjustTree(
      newImagesArr,
      tolerance,
      imageWidth,
      imageHeight
    );
    bestAspectRatioTree.addOptions(options);
    setTree(bestAspectRatioTree);
    db.collageDB.collageTree.clear();
    addTreeToDB(bestAspectRatioTree);
    db.collageDB.images.delete(imgTitle);
    db.collageDB.originalImages.delete(imgTitle);
  };

  const handleImageData = (imageDetail, originalImageDetail) => {
    setOriginalImages((arr) => arr.concat([originalImageDetail]));
    setImages((arr) => arr.concat([imageDetail]));
    addToImagesDB(imageDetail);
    addToOriginalImagesDB(originalImageDetail);
    setNewImagesLoaded((cnt) => cnt + 1);
  };

  const handleLoad = (event) => {
    helpers.handleImageLoad(
      event,
      resizeImageWidth,
      setImageCount,
      setNewImageCount,
      handleImageData
    );
  };

  const handleImageAddClick = () => {
    if (addImageRef.current) addImageRef.current.click();
  };

  const handlePaddingChange = (e) =>
    setOptions((opts) => ({ ...opts, padding: e.target.value }));
  const handleRadiusChange = (e) => {
    setOptions((opts) => ({ ...opts, borderRadius: e.target.value }));
  };

  const handleColorClick = () => setDisplayColorPicker((dcp) => !dcp);
  const handleColorClose = () => setDisplayColorPicker(false);
  const handleColorChange = (clr) =>
    setOptions((opts) => ({ ...opts, color: clr.rgb }));
  const handlePositionChange = (value) =>
    setOptions((opts) => ({ ...opts, topText: value }));

  const handleTextChange = (e) => {
    const { target } = e;
    setOptions((opts) => ({ ...opts, text: target.value }));
  };

  const handleDownload = () => setOpen(true);
  const onDownloadClose = () => setOpen(false);
  const onDownloadClick = (w, h) => {
    setPrintOptions({ paperWidth: w, paperHeight: h });
    setGenerate(true);
    setOpen(false);
  };

  const handleGenerateCallback = () => {
    setGenerate(false);
    setPrintOptions({ paperWidth: 0, paperHeight: 0 });
  };

  useEffect(() => {
    setDimensions();
    window.addEventListener('resize', setDimensions);

    return () => window.removeEventListener('resize', setDimensions);
  }, []);

  const onRefresh = () => setRefresh(true);
  const onDelete = () => setRemoveImage((ri) => !ri);
  const clearDelete = () => setRemoveImage(false);

  useEffect(() => {
    if (options.text.length > 0 && Object.keys(tree).length > 0) {
      const tH = helpers.wrapText(options.text, 0, 0, width, 20, 25);
      setTextHeight(tH.length * 25);
      const [w, h] = helpers.getCollageDimension(
        width,
        height - tH.length * 25,
        tree.root.value.aspectRatio
      );
      setImageWidth(w);
      setImageHeight(h);
    } else {
      setTextHeight(0);
      handlePositionChange(true);
      setImageWidth(width);
      setImageHeight(height);
    }
  }, [width, height, tree, options.text]);

  const downloadDialog = (
    <DownloadDialog
      open={open}
      onClick={onDownloadClick}
      onClose={onDownloadClose}
      sizes={paperSizes}
    />
  );

  const collage = (
    <Collage
      aspectRatio={aspectRatio}
      tree={tree}
      images={images}
      originalImages={originalImages}
      imageHeight={imageHeight}
      imageWidth={imageWidth}
      canvasHeight={height}
      canvasWidth={width}
      removeImage={removeImage}
      handleRemoveImage={handleRemoveImage}
      options={options}
      printOptions={printOptions}
      collageCallback={collageCallback}
      generate={generate}
      generateCallback={handleGenerateCallback}
    />
  );

  const mobileControls = (
    <MobileControls
      options={options}
      handlePaddingChange={handlePaddingChange}
      handleRadiusChange={handleRadiusChange}
      handleColorClick={handleColorClick}
      handleColorClose={handleColorClose}
      handleColorChange={handleColorChange}
      handleTextChange={handleTextChange}
      handlePositionChange={handlePositionChange}
      displayColorPicker={displayColorPicker}
      controlsRef={footerRef}
      onRefresh={onRefresh}
      onDelete={onDelete}
      onAdd={handleImageAddClick}
      clearDelete={clearDelete}
      handleClear={handleClear}
    />
  );

  const header = <Header headerRef={headerRef} />;
  const desktopPhotosComp = (
    <DesktopImageOptions
      onDelete={onDelete}
      onAdd={handleImageAddClick}
      clearDelete={clearDelete}
    />
  );
  const desktopEditComp = (
    <EditControl
      options={options}
      handlePaddingChange={handlePaddingChange}
      handleRadiusChange={handleRadiusChange}
      handleColorClick={handleColorClick}
      handleColorClose={handleColorClose}
      handleColorChange={handleColorChange}
      displayColorPicker={displayColorPicker}
      handleTextChange={handleTextChange}
      handlePositionChange={handlePositionChange}
    />
  );
  const desktopControls = (
    <DesktopControls
      onRefresh={onRefresh}
      photosComp={desktopPhotosComp}
      editComp={desktopEditComp}
      handleClear={handleClear}
      clearDelete={clearDelete}
    />
  );
  const isTreeEmpty = Object.keys(tree).length === 0;
  const isMobile = window.innerWidth < window.innerHeight;
  const controls = isMobile ? mobileControls : desktopControls;

  const actions = isMobile ? (
    <MobileActions actionsRef={actionsRef} onDownload={handleDownload} />
  ) : (
    <DesktopActions onDownload={handleDownload} />
  );

  return (
    <>
      <View
        open={open}
        loading={loading}
        width={width}
        height={height}
        maxWidth={maxWidth}
        maxHeight={maxHeight}
        imageHeight={imageHeight}
        images={images}
        imgCount={imgCount}
        handleLoad={handleLoad}
        isTreeEmpty={isTreeEmpty}
        header={header}
        actions={actions}
        downloadDialog={downloadDialog}
        collage={collage}
        controls={controls}
      />
      <input
        ref={addImageRef}
        type="file"
        hidden
        multiple
        accept="image/*"
        onChange={handleLoad}
      />
      {generate ? <Loading title="Creating collage ..." /> : null}
    </>
  );
}

export default CollageMaker;
