import PropTypes from 'prop-types';
import DBNA from 'components/collage/DesktopActionItem';
import ControlsDivider from 'components/collage/ControlsDivider';
import List from '@mui/material/List';
import DeleteIcon from '@mui/icons-material/Delete';
import AddPhotoAlternateIcon from '@mui/icons-material/AddPhotoAlternate';
import { alpha } from '@mui/material/styles';

export default function DesktopImageOptions({ onDelete, onAdd, clearDelete }) {
  const handleAdd = () => {
    clearDelete();
    onAdd();
  };

  return (
    <List
      sx={{
        backgroundColor: alpha('#fff', 0.15),
        borderRadius: '10px 10px 10px 10px',
        height: 'fit-content',
      }}
    >
      <DBNA
        label="Add"
        icon={<AddPhotoAlternateIcon />}
        handleChange={handleAdd}
      />
      <ControlsDivider />
      <DBNA label="Delete" icon={<DeleteIcon />} handleChange={onDelete} />
    </List>
  );
}

DesktopImageOptions.propTypes = {
  onDelete: PropTypes.func.isRequired,
  onAdd: PropTypes.func.isRequired,
  clearDelete: PropTypes.func.isRequired,
};
