import PropTypes from 'prop-types';
import { useState } from 'react';
import { alpha } from '@mui/material/styles';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
//  import FormLabel from '@mui/material/FormLabel';
import TextField from '@mui/material/TextField';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';

export default function TextControl({
  text,
  handleTextChange,
  topText,
  handlePositionChange,
}) {
  const [collageTextError] = useState('');
  return (
    <Stack
      sx={{
        backgroundColor: 'transparent',
      }}
    >
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        spacing={2}
      >
        <Typography
          sx={{ fontWeight: 'bold', color: 'gray', fontSize: '0.87rem' }}
        >
          Collage Text
        </Typography>
        <FormGroup row>
          <FormControlLabel
            disableTypography
            control={
              <Checkbox
                size="small"
                checked={topText === false}
                onChange={() => handlePositionChange(false)}
                sx={{ color: 'white', '&.Mui-checked': { color: 'white' } }}
              />
            }
            label={
              <Typography fontSize="0.8rem" fontWeight="bold">
                Bottom
              </Typography>
            }
            sx={{ color: 'gray', fontWeight: 'bold' }}
          />
          <FormControlLabel
            disableTypography
            control={
              <Checkbox
                size="small"
                checked={topText === true}
                onChange={() => handlePositionChange(true)}
                sx={{ color: 'gray', '&.Mui-checked': { color: 'white' } }}
              />
            }
            label={
              <Typography fontSize="0.8rem" fontWeight="bold">
                Top
              </Typography>
            }
            sx={{ color: 'gray', fontWeight: 'bold', fontSize: '0.8rem' }}
          />
        </FormGroup>
      </Stack>
      <TextField
        variant="outlined"
        label=""
        defaultValue={text}
        onChange={handleTextChange}
        size="small"
        sx={{ width: '100%', mb: 1 }}
        InputProps={{ sx: { backgroundColor: alpha('#fff', 0.9) } }}
        helperText={
          <Typography sx={{ color: 'gray', fontSize: '0.75rem' }}>
            {collageTextError}
          </Typography>
        }
        error={!!collageTextError}
      />
    </Stack>
  );
}

TextControl.propTypes = {
  text: PropTypes.string.isRequired,
  handleTextChange: PropTypes.func.isRequired,
  topText: PropTypes.bool.isRequired,
  handlePositionChange: PropTypes.func.isRequired,
};
