import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import { alpha } from '@mui/material/styles';
import Node from './node';
import helpers from './helpers';

const convertToRGBA = (value) =>
  `rgba(${value.r}, ${value.g}, ${value.b}, ${value.a})`;

function TextDisplay(textArray, position) {
  return (
    <>
      {
        // eslint-disable-next-line react/destructuring-assignment
        textArray.map((chunk) => (
          <Typography
            key={chunk[2]}
            sx={{
              width: '100%',
              position: 'absolute',
              top: position + chunk[2],
              left: '50%',
              transform: 'translate(-50%, 0)',
              bgcolor: alpha('#1c1c1c', 0.7),
              paddingX: 1,
              color: 'button.main',
              margin: 'auto',
              textAlign: 'center',
              display: 'inline-block',
              fontFamily: 'Arial',
              fontSize: '20px',
              lineHeight: '25px',
            }}
          >
            {chunk[0]}
          </Typography>
        ))
      }
    </>
  );
}

function TextCanvasDraw(
  ctx,
  textArray,
  position,
  scaleFactor,
  canvasWidth,
  textHeight
) {
  ctx.font = `${scaleFactor * 20}px Arial`;
  ctx.textAlign = 'center';
  ctx.textBaseline = 'top';
  ctx.fillStyle = '#1c1c1cb3';
  ctx.fillRect(0, position, canvasWidth, position + textHeight);
  ctx.fillStyle = 'white';

  console.log('text array', textArray, textHeight);
  // eslint-disable-next-line no-restricted-syntax
  for (const chunk of textArray) {
    const [textPart, , y] = chunk;
    ctx.fillText(textPart, canvasWidth / 2, y + position + 2.5 * scaleFactor);
  }
}

export default class BinaryTree {
  constructor(value) {
    if (this.root === undefined) {
      this.root = new Node(value);
      return this;
    }
  }

  addOptions(options) {
    //  const convertToRGBA = (value) => `rgba(${value.r}, ${value.g}, ${value.b}, ${value.a})`;
    this.root.options = { ...options };
  }

  display() {
    if (this.root !== null) {
      return this.root.display();
    }
    return null;
  }

  replaceImages(newImages) {
    if (this.root !== null) {
      this.root.replaceImages(newImages);
    }
  }

  fromObject(obj) {
    if (obj.root != null) this.root.fromObject(obj.root);
    return this;
  }

  removeImageData() {
    const tempTree = this.copy();
    if (this.root !== null) {
      tempTree.root.removeImageData();
    }
    return tempTree;
  }

  addImageData(imageData) {
    if (this.root !== null) {
      this.root.addImageData(imageData);
    }
  }

  DOMDisplay(canvasWidth, canvasHeight, delImg, handleRemoveImage) {
    const { text, topText, padding, borderRadius, color } = this.root.options;
    const { width, height } = this.root.value;
    const lA = helpers.wrapText(text, 0, 0, canvasWidth, 20, 25);
    const textHeight = text.length ? lA.length * 25 : 0;
    let yOffset = (canvasHeight - textHeight - height) / 2;
    const xOffset = (canvasWidth - width) / 2;
    let position = canvasHeight - textHeight;
    if (topText && text.length > 0) {
      yOffset += textHeight;
      position = 0;
    }

    console.log(
      'tree dom display',
      canvasWidth,
      canvasHeight,
      this.root.options,
      textHeight,
      yOffset,
      xOffset,
      width,
      this
    );
    if (this.root !== null) {
      return (
        <Box
          width={canvasWidth}
          height={canvasHeight}
          sx={{
            position: 'relative',
            backgroundColor: convertToRGBA(color),
            outline: '1px solid black',
          }}
        >
          {this.root.DOMDisplay(
            padding,
            borderRadius,
            delImg,
            handleRemoveImage,
            yOffset,
            xOffset
          )}
          {text.length ? TextDisplay(lA, position) : null}
        </Box>
      );
    }
    return null;
  }

  CanvasDisplay(canvasWidth, canvasHeight, canvasElement, scaleFactor) {
    if (this.root !== null) {
      const { text, topText, padding, borderRadius, color } = this.root.options;
      const { width, height } = this.root.value;
      const lA = helpers.wrapText(
        text,
        0,
        0,
        canvasWidth,
        20 * scaleFactor,
        25 * scaleFactor
      );
      const textHeight = text.length ? lA.length * 25 * scaleFactor : 0;
      let yOffset = (canvasHeight - textHeight - height) / 2;
      const xOffset = (canvasWidth - width) / 2;
      let position = canvasHeight - textHeight;
      if (topText && text.length > 0) {
        yOffset += textHeight;
        position = 0;
      }
      const ctx = canvasElement.getContext('2d');
      ctx.fillStyle = convertToRGBA(color);
      ctx.fillRect(0, 0, canvasElement.width, canvasElement.height);
      this.root.CanvasDisplay(
        canvasElement,
        padding * scaleFactor,
        borderRadius * scaleFactor,
        yOffset,
        xOffset
      );
      TextCanvasDraw(ctx, lA, position, scaleFactor, canvasWidth, textHeight);
    }
  }

  generateCollageTree(images, tar) {
    this.root.generate(images, tar, images.length);
  }

  adjust(th) {
    let trys = 0;
    let bestAdjust = this.copy();
    let bestAspectRatio = this.root.value.aspectRatio;
    const tolerableDifference = this.root.value.targetAspectRatio * (1 - th);
    let ajstCnt = 1000;
    while (trys < 100 && ajstCnt > 0) {
      ajstCnt = this.root.adjust(th);
      trys += 1;
      const diff =
        this.root.value.aspectRatio - this.root.value.targetAspectRatio;
      // console.log('adjust', this.root.value.aspectRatio, this.root.value.targetAspectRatio);
      if (
        // (diff > 0 && this.root.value.aspectRatio < bestAspectRatio)
        // || (bestAspectRatio < this.root.value.targetAspectRatio
        //   && this.root.value.aspectRatio > this.root.value.targetAspectRatio)
        Math.abs(diff) <
        Math.abs(bestAspectRatio - this.root.value.targetAspectRatio)
      ) {
        bestAspectRatio = this.root.value.aspectRatio;
        bestAdjust = this.copy();
      }
      if ((diff > 0 && diff < tolerableDifference) || diff === 0) break;
    }

    return [bestAspectRatio, bestAdjust];
  }

  copy() {
    const copyTree = new BinaryTree({});
    copyTree.root = this.root.copy();
    copyTree.root.options = { ...this.root.options };
    return copyTree;
  }

  addSize(canvasWidth, canvasHeight, aspectRatio) {
    const [maxW, maxH] = helpers.getCollageDimension(
      canvasWidth,
      canvasHeight,
      aspectRatio
    );
    this.root.value.width = maxW;
    this.root.value.height = maxH;
    console.log('add size', this.root.value.width, this.root.value.height);
    this.root.value.x = 0;
    this.root.value.y = 0;
    const { width, height, x, y, cut } = this.root.value;
    if (this.root.left !== null)
      this.root.left.addSize(width, height, x, y, cut, 'left');
    if (this.root.right !== null)
      this.root.right.addSize(width, height, x, y, cut, 'right');
  }
}
