import Dexie from 'dexie';

const collageDB = new Dexie('collage');

collageDB.version(1).stores({
  images: 'title, url,naturalWidth,naturalHeight,aspectRatio', // Primary key and indexed props
  originalImages: 'title, url,naturalWidth,naturalHeight,aspectRatio', // Primary key and indexed props
  imgCount: '++id, count',
  collageTree: '++id, collageTree',
});

export default { collageDB };
