import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import Typography from '@mui/material/Typography';
import PaddingIcon from '@mui/icons-material/Padding';
import CropFreeIcon from '@mui/icons-material/CropFree';
import SquareIcon from '@mui/icons-material/Square';
import CloseIcon from '@mui/icons-material/Close';
import TitleIcon from '@mui/icons-material/Title';
import { alpha } from '@mui/material/styles';

export default function EditOptions({
  color,
  editOptionsValue,
  open,
  handleOptionsClose,
  handleEditOptionsClick,
}) {
  return open ? (
    <Box sx={{ position: 'relative' }}>
      <BottomNavigation
        showLabels
        value={editOptionsValue}
        onChange={handleEditOptionsClick}
        sx={{
          display: 'flex',
          fontWeight: 'bold',
          backgroundColor: alpha('#fff', 0.2),
          borderTop: '1px solid gray',
          position: 'relative',
          alignItems: 'center',
        }}
      >
        <BottomNavigationAction
          label={
            <Typography fontSize="0.70rem" fontWeight="bold">
              Padding
            </Typography>
          }
          value="padding"
          icon={<PaddingIcon />}
          sx={{
            fontWeight: 'bold',
            color: 'gray',
            '&.Mui-selected': { color: 'white', outline: 'none' },
          }}
        />
        <BottomNavigationAction
          label={
            <Typography fontSize="0.70rem" fontWeight="bold">
              Corner
            </Typography>
          }
          value="border radius"
          icon={<CropFreeIcon />}
          sx={{
            fontWeight: 'bold',
            color: 'gray',
            '&.Mui-selected': { color: 'white', outline: 'none' },
          }}
        />
        <BottomNavigationAction
          label={
            <Typography fontSize="0.70rem" fontWeight="bold">
              Color
            </Typography>
          }
          value="color"
          icon={
            <SquareIcon
              sx={{
                fill: `rgba(${color.r}, ${color.g}, ${color.b}, ${color.a})`,
                borderRadius: '5px',
              }}
            />
          }
          sx={{
            fontWeight: 'bold',
            color: 'gray',
            '&.Mui-selected': { color: 'white', outline: 'none' },
          }}
        />
        <BottomNavigationAction
          label="Text"
          value="text"
          icon={<TitleIcon />}
          sx={{
            fontWeight: 'bold',
            color: 'gray',
            '&.Mui-selected': { color: 'white', outline: 'none' },
          }}
        />
      </BottomNavigation>
      <CloseIcon
        fontSize="1rem"
        sx={{
          position: 'absolute',
          top: 5,
          right: 5,
          fill: 'white',
          backgroundColor: 'gray',
          borderRadius: '50%',
          padding: '2px',
        }}
        onClick={handleOptionsClose}
      />
    </Box>
  ) : null;
}

EditOptions.propTypes = {
  editOptionsValue: PropTypes.string.isRequired,
  open: PropTypes.bool.isRequired,
  handleOptionsClose: PropTypes.func.isRequired,
  handleEditOptionsClick: PropTypes.func.isRequired,
  color: PropTypes.shape({
    r: PropTypes.number.isRequired,
    g: PropTypes.number.isRequired,
    b: PropTypes.number.isRequired,
    a: PropTypes.number.isRequired,
  }).isRequired,
};
