import { useState, useEffect } from 'react';
import db from 'db';
import BinaryTree from 'components/collage/binaryTree';

export default function useCollageTree(
  initialValue,
  loadingCallback = () => {}
) {
  const tableName = 'collageTree';
  const [value, setValue] = useState(initialValue);

  async function setDataFromDB() {
    const rows = await db.collageDB[tableName].toArray();
    if (rows.length > 0) {
      const newTree = new BinaryTree({});
      newTree.fromObject({ root: rows[0].root });
      if ('options' in rows[0].root) {
        loadingCallback((opts) => ({ ...opts, ...rows[0].root.options }));
      }
      setValue(newTree);
    }
  }

  useEffect(() => {
    setDataFromDB();
  }, []);

  const addToDB = async (data) => {
    const cleanedData = data.removeImageData();
    await db.collageDB[tableName].put(cleanedData);
  };

  return [value, setValue, addToDB];
}
