import Box from '@mui/material/Box';
//  import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import DeleteIcon from '@mui/icons-material/Delete';
import Tooltip from '@mui/material/Tooltip';
import { alpha } from '@mui/material/styles';
import helpers from 'components/collage/helpers';

export default class Node {
  constructor(value) {
    this.value = value;
    this.left = null;
    this.right = null;
  }

  fromObject(obj) {
    if (obj != null) {
      this.value = obj.value;
      if (obj.left != null) {
        this.left = new Node(obj.left.value);
        this.left.fromObject(obj.left);
      }
      if (obj.right != null) {
        this.right = new Node(obj.right.value);
        this.right.fromObject(obj.right);
      }
      return this;
    }
    return null;
  }

  display() {
    let output = [];
    const { cut, title, aspectRatio } = this.value;
    if (this === null) return null;
    if ('cut' in this.value) output.push([cut, aspectRatio]);
    if ('title' in this.value) output.push([title, aspectRatio]);
    if (this.left !== null) output = output.concat(this.left.display());
    if (this.right !== null) output = output.concat(this.right.display());
    return output;
  }

  copy() {
    if (this === null) return null;
    const copyNode = new Node({ ...this.value });
    if (this.left != null) copyNode.left = this.left.copy();
    if (this.right != null) copyNode.right = this.right.copy();
    return copyNode;
  }

  replaceImages(newImages) {
    if (this !== null) {
      if ('title' in this.value) {
        const findIndex = newImages.findIndex(
          (elem) => elem.title === this.value.title
        );
        const origImage = newImages[findIndex];
        this.value.src = origImage.src;
        this.value.naturalWidth = origImage.naturalWidth;
        this.value.naturalHeight = origImage.naturalHeight;
      }
      if (this.left != null) this.left.replaceImages(newImages);
      if (this.right != null) this.right.replaceImages(newImages);
    }
  }

  removeImageData() {
    if (this !== null) {
      if ('title' in this.value) {
        this.value.src = '';
      }
      if (this.left != null) this.left.removeImageData();
      if (this.right != null) this.right.removeImageData();
    }
  }

  addImageData(imageData) {
    if (this !== null) {
      if ('title' in this.value) {
        const image = imageData.find((elem) => elem.title === this.value.title);
        this.value.src = image.src;
      }
      if (this.left != null) this.left.addImageData(imageData);
      if (this.right != null) this.right.addImageData(imageData);
    }
  }

  RenderImage(
    padding,
    borderRadius,
    delImg,
    handleRemoveImage,
    yOffset,
    xOffset
  ) {
    const { width, title, src, x, y } = this.value;

    const newX = x + xOffset;
    const newY = y + yOffset;

    return (
      <>
        <Box
          key={title}
          component="img"
          title={title}
          alt={title}
          width={width}
          src={src}
          sx={{
            position: 'absolute',
            padding: `${padding}px`,
            borderRadius: `${borderRadius}px`,
            left: this.value.x + xOffset,
            top: this.value.y + yOffset,
          }}
        />
        {/*
         *<Typography
         *  sx={{
         *    position: 'absolute',
         *    top: newY,
         *    left: newX,
         *    backgroundColor: 'white',
         *    fontSize: '15px',
         *  }}
         *>
         *  {newX.toFixed(2)}
         *  ,
         *  {newY.toFixed(2)}
         *</Typography>
         */
        /*
         *<Typography
         *  sx={{
         *    position: 'absolute',
         *    top: this.value.y + yOffset + padding,
         *    left: this.value.x + xOffset + padding,
         *    backgroundColor: 'white',
         *  }}
         *>
         *  {width.toFixed(2)}
         *  ,
         *  {height.toFixed(2)}
         *</Typography>
         */
        /*
         *<Typography
         *  sx={{
         *    position: 'absolute',
         *    bottom: 38,
         *    right: 10,
         *    backgroundColor: 'white',
         *    fontSize: '0.5rem',
         *    fontWeight: 700,
         *  }}
         *>
         *  {title}
         *</Typography>
         */}

        {delImg === true ? (
          <Tooltip title="Remove Image">
            <IconButton
              size="medium"
              sx={{
                color: alpha('#000', 0.7),
                bgcolor: alpha('#fff', 0.5),
                position: 'absolute',
                top: newY + padding,
                left: newX + padding,
                padding: `${padding}px`,
                borderRadius: '0 0 5px 0',
              }}
              onClick={() => handleRemoveImage(title)}
            >
              <DeleteIcon size="medium" />
            </IconButton>
          </Tooltip>
        ) : null}
      </>
    );
  }

  DOMDisplay(
    padding,
    borderRadius,
    delImg,
    handleRemoveImage,
    yOffset,
    xOffset
  ) {
    console.log(
      'node dom display',
      padding,
      borderRadius,
      delImg,
      handleRemoveImage,
      yOffset,
      xOffset
    );
    const { cut } = this.value;
    if (this === null) return null;
    if (cut) {
      console.log(this.right, this.left);
      return (
        <>
          {this.left != null
            ? this.left.DOMDisplay(
                padding,
                borderRadius,
                delImg,
                handleRemoveImage,
                yOffset,
                xOffset
              )
            : null}
          {this.right != null
            ? this.right.DOMDisplay(
                padding,
                borderRadius,
                delImg,
                handleRemoveImage,
                yOffset,
                xOffset
              )
            : null}
        </>
      );
    }
    if ('title' in this.value)
      return this.RenderImage(
        padding,
        borderRadius,
        delImg,
        handleRemoveImage,
        yOffset,
        xOffset
      );
    return null;
  }

  ResizeImage(image) {
    const tempCanvas = document.createElement('canvas');
    tempCanvas.width = this.value.naturalWidth;
    tempCanvas.height = this.value.naturalHeight;
    const tempCtx = tempCanvas.getContext('2d');
    let width = this.value.naturalWidth;
    let height = this.value.naturalHeight;
    let newWidth;
    let newHeight;
    const scaleFactor = Math.floor(
      Math.log2(this.value.naturalWidth / this.value.width)
    );
    if (scaleFactor === 0) {
      tempCtx.drawImage(
        image,
        0,
        0,
        width,
        height,
        0,
        0,
        this.value.width,
        this.value.height
      );
    } else {
      for (let i = 0; i < scaleFactor; i += 1) {
        newWidth = width >> 1; // eslint-disable-line no-bitwise
        newHeight = newWidth / this.value.aspectRatio; // eslint-disable-line no-bitwise
        if (i === 0) {
          tempCtx.drawImage(
            image,
            0,
            0,
            width,
            height,
            0,
            0,
            newWidth,
            newHeight
          );
        } else {
          tempCtx.drawImage(
            tempCanvas,
            0,
            0,
            width,
            height,
            0,
            0,
            newWidth,
            newHeight
          );
        }
        width = newWidth;
        height = newHeight;
      }

      tempCtx.drawImage(
        tempCanvas,
        0,
        0,
        width,
        height,
        0,
        0,
        this.value.width,
        this.value.height
      );
    }
    return tempCanvas;
  }

  ImageStyling(ctx, tempCanvas, padding, radius, yOffset, xOffset) {
    const x = this.value.x + padding + xOffset;
    const y = this.value.y + padding + yOffset;
    const clipwidth = this.value.width - 2 * padding;
    const clipheight = clipwidth / this.value.aspectRatio;

    ctx.save();
    ctx.beginPath();
    ctx.moveTo(x + radius, y);
    ctx.lineTo(x + clipwidth - radius, y);
    ctx.quadraticCurveTo(x + clipwidth, y, x + clipwidth, y + radius);
    ctx.lineTo(x + clipwidth, y + clipheight - radius);
    ctx.quadraticCurveTo(
      x + clipwidth,
      y + clipheight,
      x + clipwidth - radius,
      y + clipheight
    );
    ctx.lineTo(x + radius, y + clipheight);
    ctx.quadraticCurveTo(x, y + clipheight, x, y + clipheight - radius);
    ctx.lineTo(x, y + radius);
    ctx.quadraticCurveTo(x, y, x + radius, y);
    ctx.closePath();
    ctx.clip();

    ctx.drawImage(
      tempCanvas,
      0,
      0,
      this.value.width,
      this.value.height,
      x,
      y,
      clipwidth,
      clipheight
    );
    ctx.restore();
  }

  CanvasDisplay(canvasElement, padding, radius, yOffset, xOffset) {
    if ('cut' in this.value) {
      this.left.CanvasDisplay(canvasElement, padding, radius, yOffset, xOffset);
      this.right.CanvasDisplay(
        canvasElement,
        padding,
        radius,
        yOffset,
        xOffset
      );
    }

    if ('title' in this.value) {
      const ctx = canvasElement.getContext('2d');
      const newImg = new Image();
      newImg.onload = () => {
        const tempCanvas = this.ResizeImage(newImg);
        const tempCtx = tempCanvas.getContext('2d');
        this.ImageStyling(ctx, tempCanvas, padding, radius, yOffset, xOffset);
        tempCtx.clearRect(0, 0, tempCanvas.width, tempCanvas.height);
      };
      newImg.src = this.value.src;
    }
  }

  generate(images, tar, imageCount) {
    const targetAspectRatioArr = images.map(
      (image) => image.naturalWidth / image.naturalHeight
    );
    if (imageCount < 1) return [];
    if (imageCount === 1) {
      const closestIndex = helpers.findClosest(targetAspectRatioArr, tar);
      this.value = images[closestIndex];
      this.left = null;
      this.right = null;
      return [
        ...images.slice(0, closestIndex),
        ...images.slice(closestIndex + 1),
      ];
    }

    const cut = helpers.getCut();
    this.value = { cut, targetAspectRatio: tar };
    if (imageCount === 2) {
      const [leftIndex, rightIndex] = helpers.findImgPair(
        targetAspectRatioArr,
        tar
      );
      this.value.aspectRatio = helpers.getAspectRatio(
        this.value.cut,
        images[leftIndex].aspectRatio,
        images[rightIndex].aspectRatio
      );
      this.left = new Node(images[leftIndex]);
      this.right = new Node(images[rightIndex]);
      return images.filter(
        (item, index) => ![leftIndex, rightIndex].includes(index)
      );
    }

    let restImages;
    let targetAspectRatio;
    this.left = new Node({ cut: helpers.getCut() });
    //  this.right = new Node({ cut: helpers.getCut() });
    this.right = new Node({ cut: this.left.value.cut === 'v' ? 'h' : 'v' });

    if (cut === 'v') {
      targetAspectRatio = tar * 2;
    } else {
      targetAspectRatio = tar / 2;
    }
    restImages = this.left.generate(
      images,
      targetAspectRatio,
      imageCount - Math.floor(imageCount / 2)
    );
    restImages = this.right.generate(
      restImages,
      targetAspectRatio,
      Math.floor(imageCount / 2)
    );

    this.value.aspectRatio = helpers.getAspectRatio(
      this.value.cut,
      this.left.value.aspectRatio,
      this.right.value.aspectRatio
    );

    return restImages;
  }

  adjust(th) {
    let adjustments = 0;
    if (this === null) return adjustments;

    if (this.left !== null && this.right !== null) {
      if (this.value.aspectRatio > this.value.targetAspectRatio * th) {
        this.value.cut = 'h';
        adjustments += 1;
      }
      if (this.value.aspectRatio < this.value.targetAspectRatio / th) {
        this.value.cut = 'v';
        adjustments += 1;
      }

      if (this.value.cut === 'v') {
        this.left.value.targetAspectRatio = this.value.targetAspectRatio / 2;
        this.right.value.targetAspectRatio = this.value.targetAspectRatio / 2;
      } else {
        this.left.value.targetAspectRatio = this.value.targetAspectRatio * 2;
        this.right.value.targetAspectRatio = this.value.targetAspectRatio * 2;
      }
      adjustments += this.left.adjust(th) + this.right.adjust(th);
      this.value.aspectRatio = helpers.getAspectRatio(
        this.value.cut,
        this.left.value.aspectRatio,
        this.right.value.aspectRatio
      );
    }

    return adjustments;
  }

  addSize(parentWidth, parentHeight, parentX, parentY, parentCut, nodeType) {
    if (parentCut === 'v') {
      this.value.height = parentHeight;
      this.value.width = this.value.height * this.value.aspectRatio;
    } else {
      this.value.width = parentWidth;
      this.value.height = this.value.width / this.value.aspectRatio;
    }

    if (nodeType === 'left') {
      this.value.x = parentX;
      this.value.y = parentY;
    } else if (parentCut === 'h') {
      this.value.y = parentY + parentHeight - this.value.height;
      this.value.x = parentX;
    } else {
      this.value.x = parentX + parentWidth - this.value.width;
      this.value.y = parentY;
    }

    if (this.left !== null) {
      this.left.addSize(
        this.value.width,
        this.value.height,
        this.value.x,
        this.value.y,
        this.value.cut,
        'left'
      );
    }
    if (this.right !== null) {
      this.right.addSize(
        this.value.width,
        this.value.height,
        this.value.x,
        this.value.y,
        this.value.cut,
        'right'
      );
    }
  }
}
