import { useState, useEffect } from 'react';
import helpers from './helpers';

export default function Collage({
  tree,
  images,
  originalImages,
  imageWidth,
  imageHeight,
  canvasWidth,
  canvasHeight,
  removeImage,
  handleRemoveImage,
  options,
  collageCallback,
  generate,
  generateCallback,
  printOptions,
}) {
  const [collage, setCollage] = useState(null);

  //  const getTextHeight = () => {
  //  if (options.text.length === 0) return 0;
  //  return (50 * printOptions.paperHeight) / canvasHeight;
  //  };

  const isEmpty = (t) => Object.keys(t).length === 0;
  const createCollage = () => {
    tree.addSize(imageWidth, imageHeight, tree.root.value.aspectRatio);
    tree.addOptions(options);
    tree.addImageData(images);
    const collageElem = tree.DOMDisplay(
      canvasWidth,
      canvasHeight,
      removeImage,
      handleRemoveImage
    );
    setCollage(collageElem);
    collageCallback(tree);
  };

  useEffect(() => {
    if (!isEmpty(tree)) createCollage();
  }, []);

  useEffect(() => {
    if (!isEmpty(tree)) {
      createCollage();
    } else {
      setCollage(null);
    }
  }, [tree, options, imageWidth, imageHeight, removeImage]);

  const downloadCollage = (canvas, pW, pH) => {
    canvas.toBlob((blob) => {
      const link = document.createElement('a');
      link.download = `collage-${pW}X${pH}-${Date.now()}.png`;
      link.href = URL.createObjectURL(blob);
      link.click();
      URL.revokeObjectURL(link.href);
      link.href = null;
      generateCallback();
    });
  };

  useEffect(() => {
    if (generate === true) {
      const canvasElement = document.createElement('canvas');
      const { paperWidth, paperHeight } = printOptions;
      canvasElement.width = paperWidth;
      canvasElement.height = paperHeight;
      const scaleFactor = paperWidth / canvasWidth;
      const lineArray = helpers.wrapText(
        options.text,
        0,
        0,
        paperWidth,
        20 * scaleFactor,
        25 * scaleFactor
      );
      const textHeight = lineArray.length * 25 * scaleFactor;
      const [imgPrintWidth, imgPrintHeight] = helpers.getCollageDimension(
        paperWidth,
        paperHeight - textHeight,
        tree.root.value.aspectRatio
      );
      const canvasTree = tree.copy();
      canvasTree.replaceImages(originalImages);
      canvasTree.addSize(
        imgPrintWidth,
        imgPrintHeight,
        tree.root.value.aspectRatio
      );
      canvasTree.CanvasDisplay(
        paperWidth,
        paperHeight,
        canvasElement,
        scaleFactor
      );

      setTimeout(
        () => downloadCollage(canvasElement, paperWidth, paperHeight),
        '5000'
      );
    }
  }, [generate]);
  return collage;
}
