import { useState, useEffect } from 'react';
import db from 'db';

export default function useIndexedDBState(
  tableName,
  initialValue,
  loadingCallback = () => {}
) {
  const [value, setValue] = useState(initialValue);

  useEffect(() => {
    async function setDataFromDB() {
      loadingCallback(true);
      const rows = await db.collageDB[tableName].toArray();
      loadingCallback(false);
      if (rows.length) setValue(rows);
    }
    setDataFromDB();
  }, []);

  const addToDB = (data) => db.collageDB[tableName].put(data);

  return [value, setValue, addToDB];
}
