import { useState, useEffect } from 'react';
import db from 'db';

export default function useImageCount(tableName, initialValue) {
  const [value, setValue] = useState(initialValue);

  async function setDataFromDB() {
    const rows = await db.collageDB[tableName].toArray();
    console.log('rows data', rows, tableName);
    if (rows.length) setValue(rows[0].count);
  }
  useEffect(() => setDataFromDB(), []);

  const addToDB = (data) => {
    console.log('data', data, tableName);
    db.collageDB[tableName].put(data);
  };

  useEffect(() => {
    db.collageDB[tableName].clear();
    console.log('Adding data to table', tableName, value);
    addToDB({ count: value });
  }, [value]);

  return [value, setValue];
}
